import React, { useState } from 'react';
import axios from 'axios';
import '../css/MetNotice.css';
import Calendar from './Calendar';
import { url } from '../config';


const MetNotice = ({ selectedPatient, userInfo }) => {
    const [notice, setNotice] = useState('');
    const [isPopupOpen, setIsPopupOpen] = useState(false); //     

    const handleInputChange = (e) => {
        setNotice(e.target.value);
    };

    const handleSaveClick = async () => {
        if (notice.trim() === '') {
            alert(' ');
            return;
        }

        try {
            const response = await axios.post(`${url}/uploadTestNotice?testNum=${selectedPatient.testNum}&metNum=${userInfo.empNum}&testNotice=${notice}`);
            console.log('  :', response.data);
            setNotice(''); //     
        } catch (error) {
            console.error('    :', error);
            alert('  ');
        }
    };

    //      
    const handleMiniCalendarClick = () => {
        //         .
        setIsPopupOpen(true);
    };

    //  
    const handleClosePopup = () => {
        setIsPopupOpen(false);
    };

    return (
        <div className='metmain'>
            <div className='notice-box'>
                <div className='mettitle-box'>
                    <img className='meticon' src='./img/MetNotice.png' alt='Met Icon'/>
                    <span className='mettitle'> </span>
                </div>
                    <textarea 
                        value={notice} 
                        onChange={handleInputChange}
                        className='noti-input'
                    />
                <button className='noti-button' onClick={handleSaveClick}></button>
            </div>
            <div className='calendar-box' onClick={handleMiniCalendarClick}>
                <div className='mettitle-box'>
                    <img className='meticon' src='./img/CalendarMin.png' alt='Met Icon'/>
                    <span className='mettitle'></span>
                </div>
                <div >
                    <Calendar className='calendar-mini' />
                </div>
            </div>

             {/* CalendarMini     */}
             {isPopupOpen && (
                 <Calendar isOpen={true}
                 onClose={handleClosePopup} 
                 onDateSelect={() => {}} />
            )}
         </div>
     )
 };

export default MetNotice;
